import { useState, useEffect } from 'react'
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import useLogin from '../service/hooks/useLogin'
const StyledWrapper = styled.div`
.box{
    flex-direction: column;
    /* gap: 10px; */
    .title{

    }
}
`;
export default function Login() {
    const { login, data, error, loading } = useLogin()
    const navigate = useNavigate();
    const [inputs, setInputs] = useState({ email: "", password: "" })
    const handleLogin = () => {
        login(inputs)
    }
    const handleInput = (evt) => {
        const { value } = evt.target;
        const { type } = evt.target.dataset;
        console.log({ inputs, type, value });
        setInputs(prev => {
            return { ...prev, [type]: value }
        })
    }
    useEffect(() => {
        if (data && !error) {
            navigate('/')
        }
    }, [data, error])
    const { email, password } = inputs;
    return (
        <StyledWrapper className="flex items-center justify-center w-screen h-screen">
            <div className="box flex items-center gap-4">
                <h1 className="title text-3xl">Rust Chat</h1>
                <input onChange={handleInput} data-type="email" value={email} type="text" className="p-2 outline-none rounded w-fit border-2" placeholder="username/email please" />
                <input type="password" onChange={handleInput} data-type="password" value={password} className="p-2 outline-none rounded w-fit border-2" placeholder="password please" />
                <button disabled={loading} className="p-2 bg-sky-500 rounded text-white" onClick={handleLogin}>Login</button>
            </div>
        </StyledWrapper>
    )
}
