import { useEffect } from 'react'
import { Outlet, useNavigate } from 'react-router-dom'
function App() {
  const navigate = useNavigate()
  useEffect(() => {
    const token = localStorage.getItem('TOKEN')
    if (!token) {
      navigate('/login')
    }
  }, [])
  return (
    <div>
      <h1>rustchat</h1>
      <Outlet />
    </div>
  );
}

export default App;
