import axios from 'axios'
import { BASE_URL, REFRESH_TOKEN, TOKEN, TOKEN_HEADER } from './config'
// import { useNavigate } from 'react-router-dom'

const instance = axios.create({
    baseURL: BASE_URL,
    headers: {
        "Content-Type": "application/json",
    },
});

instance.interceptors.request.use(
    (config) => {
        console.log("request config", config);
        const token = localStorage.getItem(TOKEN);
        if (token && config.url !== "/token/login") {
            config.headers[TOKEN_HEADER] = token; // for back-end
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

instance.interceptors.response.use(
    (res) => {
        return res;
    },
    async (err) => {
        const originalConfig = err.config;
        if (originalConfig.url !== "/token/login" && err.response) {
            // Access Token was expired
            if (err.response.status === 401 && !originalConfig._retry) {
                originalConfig._retry = true;
                try {
                    const rs = await instance.post("/token/renew", {
                        token: localStorage.getItem(TOKEN),
                        refresh_token: localStorage.getItem(REFRESH_TOKEN),
                    });
                    const { refresh_token, token } = rs.data;
                    localStorage.setItem(TOKEN, token)
                    localStorage.setItem(REFRESH_TOKEN, refresh_token)
                    return instance(originalConfig);
                } catch (_error) {
                    return Promise.reject(_error);
                }
            }
        }

        return Promise.reject(err);
    }
);

export default instance;

