import React from 'react'
import StyledWrapper from './styled'
import { useNavigate } from 'react-router-dom'

export default function Main() {
    const navigate = useNavigate()
    const handleLogout = () => {
        localStorage.removeItem("TOKEN")
        navigate("/login")
    }
    return (
        <StyledWrapper className="flex w-full">
            <div className="col p-2 bg-slate-300 servers w-[150px]">
                <div className="server rounded-lg w-[100px] h-[100px] bg-cyan-500 flex items-center justify-center">
                    Privoce
                </div>
                <div className="server rounded-lg w-[100px] h-[100px] bg-cyan-500 flex items-center justify-center">
                    Rustchat
                </div>
            </div>
            <div className="col p-2 bg-slate-500 channels w-[350px]">
                channels
                <button onClick={handleLogout}>logout</button>
            </div>
            <div className="col p-2 bg-green-200 chat w-full">
                chat
            </div>
        </StyledWrapper>
    )
}
